import { Grid } from '@mui/material'

import ClubPreview, { type ClubPreviewProps } from 'src/components/ClubPreview'

interface ClubListProps {
  clubs: ClubPreviewProps[]
}

function ClubList({ clubs }: ClubListProps) {
  return (
    <Grid container spacing={2}>
      {clubs &&
        clubs.map(club => (
          <Grid item xs={12} sm={6} md={4} lg={3} key={`${club.id} ${club.name}`}>
            <ClubPreview {...club} />
          </Grid>
        ))}
    </Grid>
  )
}

export default ClubList
